.message {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-start;
    gap: 12px;
}

.imageAndText img {
    align-self: end;
    width: 48px;
    height: 48px;
}

.text {
    position: relative;
    min-height: 60px;
    padding: 8px 12px;
    box-shadow: 0px 1px 2px 0px rgba(29, 33, 38, 0.1),0px 5px 20px 0px rgba(29, 33, 38, 0.03);
    background: rgb(0, 102, 204);
    border-radius: 10px 10px 0 10px;
}

.text:after {
    box-shadow: 0px 3px 2px -2px rgba(29, 33, 38, 0.1),0px 5px 20px -20px rgba(29, 33, 38, 0.03);
    position: absolute;
    right: -12px;
    bottom: 0;
    content: '';
    border: 7px solid transparent;
    border-left: 7px solid rgb(0, 102, 204);
    border-bottom: 7px solid rgb(0, 102, 204);
}

.name {
    text-align: right;
    justify-self: flex-start;
    color: rgb(255, 255, 255);
    font-family: Montserrat, sans-serif;
    font-size: 18px;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: 0;
    margin-bottom: 3px;
}

.messageText {
    color: rgb(255, 255, 255);
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0;
    text-align: right;
}

.time {
    width: 28px;
    color: rgb(0, 0, 0);
    font-family: Montserrat, sans-serif;
    font-size: 10px;
    font-weight: 600;
    line-height: 12px;
    letter-spacing: 0;
    text-align: left;

    align-self: end;
    margin: 0 10px;
}